.app-wrapper {
    width: 1100px;
    min-height: 80vh;
    margin: 0 auto;
    display: grid;
    grid-template-areas: 'h h h '
'n c c ';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: yellow;
}

